	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"New Motor","date":"06 Oct 2004","slug":"/2004-10-06-new-motor/","tags":"Y2004"},"html":"<p>If you're reading this, then PapaScott is running on a spanking new installation of <a href=\"http://www.movabletype.org/\">Movable Type</a>. Because when you like tinkering with old cars, you sometimes have to change the motor, just to see what happens. Things are mostly working, but I have a few bolts left over I don't know what to do with. I'll just set them aside for now...</p>"},"next":{"fields":{"title":"Game 1 ALDS","date":"06 Oct 2004","slug":"/2004-10-06-game-1-alds/"}},"previous":{"fields":{"title":"Game 2 ALDS","date":"07 Oct 2004","slug":"/2004-10-07-game-2-alds/"}}},{"node":{"fields":{"title":"Game 1 ALDS","date":"06 Oct 2004","slug":"/2004-10-06-game-1-alds/","tags":"Y2004"},"html":"<p><a href=\"http://sports.yahoo.com/mlb/recap;_ylc=X3oDMTBpa2lpNnFzBF9TAzk1ODYxNzc3BHNlYwN0bQ--?gid=241005110\" title=\"Yahoo! Sports - MLB - Twins 2, Yankees 0\">Game 1 ALDS: Twins 2, Yankees 0</a> Maybe the Twins didn't want home field advantage in the playoffs. Maybe they <em>wanted</em> to play the Yankees.</p>"},"next":{"fields":{"title":"Weblogs and Politics, Part 1","date":"05 Oct 2004","slug":"/2004-10-05-weblogs-and-politics-part-1/"}},"previous":{"fields":{"title":"New Motor","date":"06 Oct 2004","slug":"/2004-10-06-new-motor/"}}},{"node":{"fields":{"title":"Weblogs and Politics, Part 1","date":"05 Oct 2004","slug":"/2004-10-05-weblogs-and-politics-part-1/","tags":"Y2004"},"html":"<p><a href=\"http://lumma.de/eintrag.php?id=760\" title=\"Weblogs und Politik - mehr Meinung! [Lummaland]\">Nico</a> wants German bloggers to start blogging more politics and give more opinions... and not necessarily the US election. Well, I'm not a German blogger, just a blogger in Germany, but here's a start.</p>\n<p>Health insurance: fixed premiums <em>(Kopfpauschale)</em> or universal insurance based on income <em>(B&uuml;rgerversicherung)</em>. All parties agree that the current system based on wages needs reform. However, merely extending the system to include more types of income is a half-hearted reform. Health care is too important to be a rich vs. poor issue. Everyone deserves the same care, all the time. Of course the costs need to be distributed fairly. The state already has an mechanism for distributing costs... the income tax system. The health care system needs to be decoupled from income, otherwise when we have a boom-and-bust economy, we'll also have boom-and-bust health care. I don't want my heath care to bust. Ever.</p>"},"next":{"fields":{"title":"jobs are for chumps","date":"05 Oct 2004","slug":"/2004-10-05-jobs-are-for-chumps/"}},"previous":{"fields":{"title":"Game 1 ALDS","date":"06 Oct 2004","slug":"/2004-10-06-game-1-alds/"}}}],"pathPrefix":"","first":false,"last":false,"index":408,"pageCount":1101}}

